import axios from 'axios';
import { get } from 'lodash';

/* Models */
import models from './models';

export default (method, params) => {

  const model = get(models, method, '');
  const endpoint = model.method === 'put'
    ? `https://jsonplaceholder.typicode.com/${model.url}/${params.id}`
    : `https://jsonplaceholder.typicode.com/${model.url}`;

  const config = {
    url: endpoint,
    method: model.method,
    data: JSON.stringify(params),
    params: model.method === 'get' && params ? params : null,
    headers: {
      "Content-type": "application/json; charset=UTF-8",
    }
  };

  const request = axios.request(config)
    .then(res => res.data)
    .catch(err => err);

  return request;
};