import {
  call,
  put,
  takeEvery,
} from 'redux-saga/effects';
import { get } from 'lodash';
import { normalize, schema } from 'normalizr';

/* API */
import api from 'api';

/* Types */
import types from './types';

const user = new schema.Entity('users', {}, { idAttribute: 'id' });

function* getUsers({ params }) {
  const id = get(params, 'id', null);

  try {
    const data = yield call(api, 'profile', id && { id });

    if (data) {
      if (id) {
        yield put({ 
          type: types.GET_USER_SUCCESS, 
          data: data[0],
        });
      } else {
        const normalizedData = normalize(data, [user]);
        yield put({ 
          type: types.GET_ALL_USERS_SUCCESS, 
          data: normalizedData.entities.users,
        });
      }
      
    }
  } catch (e) {
    yield put({ type: types.GET_PROFILE_FAILURE });
  }
}

function* getTasks({ params }) {
  const id = get(params, 'id', null);

  try {
    const data = yield call(api, 'tasks', id && { id });

    if (data) {
      yield put({ 
        type: id ? types.GET_USER_TASKS_SUCCESS : types.GET_ALL_TASKS_SUCCESS, 
        data,
      });
    }
  } catch (e) {
    yield put({ type: types.GET_TASKS_FAILURE });
  }
}

export function* mainSaga() {
  yield takeEvery(types.GET_USERS_REQUEST, getUsers);
  yield takeEvery(types.GET_TASKS_REQUEST, getTasks);
}
