import { connect } from 'react-redux';
import { compose, lifecycle } from 'recompose';

/* Actions */
import { actions } from 'views/ducks';

/* Components */
import AppComponent from './AppComponent';

const mapStateToProps = ({ router, app }) => ({ 
  location: router.location,
  ownId: app.profile.id,
});

const mapDispatchToProps = dispatch => ({
  getUsers: params => dispatch(actions.getUsers(params)),
});

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  lifecycle({
    componentDidMount() {
      const { getUsers, ownId } = this.props;

      // Если window.__DATA__.user пуст, то подгружаем данные с API
      if (!ownId) {
        getUsers({ id: 1 });
      }

      // Получаем список всех пользователей
      getUsers();
    },
  }),
)(AppComponent);