import types from './types';

const initialState = {
  profile: window.__DATA__.user || {},
  allTasks: [],
  userTasks: window.__DATA__.tasks || [],
  allUsers: {},
};

export default (state = initialState, action) => {
  switch (action.type) {
    case types.GET_USER_SUCCESS:
      return {
        ...state,
        profile: action.data,
      };
    case types.GET_ALL_USERS_SUCCESS:
      return {
        ...state,
        allUsers: action.data,
      };  
    case types.GET_ALL_TASKS_SUCCESS:
      return {
        ...state,
        allTasks: action.data,
      };
    case types.GET_USER_TASKS_SUCCESS:
      return {
        ...state,
        userTasks: action.data,
      };
    case types.UPDATE_PROFILE:
      return {
        ...state,
        profile: {
          ...state.profile,
          ...action.values,
        },
      }; 
    default:
      return state;
  }
};
